export default {
	props: {
		isShow: Boolean
	},
	data () {
		return {}
	},
	methods: {
		hidePopup () {
			this.$emit('hide')
		},
		addFriend () {
			this.$emit('addFriend')
		},
		clear () {
			this.$emit('clear')
		}
	}
}
